.box{background:#fff;overflow:hidden;width:656px;height:400px;border-radius:2px;-webkit-box-sizing:border-box;box-sizing:border-box;-webkit-box-shadow:0 0 40px #000;box-shadow:0 0 40px #000;color:#8b8c8d;font-size:0
}
.box .box-part{display:inline-block;position:relative;vertical-align:top;-webkit-box-sizing:border-box;box-sizing:border-box;height:200%;width:50%
}
.box .box-part#bp-right{background:url("/static/images.png") no-repeat top center;border-left:1px solid #eee
}
.box .box-messages{position:absolute;left:0;bottom:0;width:100%
}
.box .box-error-message{position:relative;overflow:hidden;-webkit-box-sizing:border-box;box-sizing:border-box;height:0;line-height:32px;padding:0 12px;text-align:center;width:100%;font-size:11px;color:#fff;background:#f38181
}
.box .partition{width:100%;height:100%
}
.box .partition .partition-title{-webkit-box-sizing:border-box;box-sizing:border-box;padding:30px;width:100%;text-align:center;letter-spacing:1px;font-size:20px;font-weight:300
}
.box .partition .partition-form{padding:0 20px;-webkit-box-sizing:border-box;box-sizing:border-box
}
.box input[type=email],.box input[type=text],.box textarea{display:block;-webkit-box-sizing:border-box;box-sizing:border-box;margin-bottom:4px;width:100%;font-size:12px;line-height:2;border:0;border-bottom:1px solid #dddedf;padding:4px 8px;font-family:inherit;-webkit-transition:.5s all;transition:.5s all;outline:none
}
.box input[type=email]#n-bio,.box input[type=text]#n-bio,.box textarea#n-bio{width:300px;height:100px;border-radius:4px;border:1px solid #dddedf
}
.box button{background:#fff;border-radius:4px;-webkit-box-sizing:border-box;box-sizing:border-box;padding:10px;letter-spacing:1px;font-family:"Open Sans",sans-serif;font-weight:400;min-width:140px;margin-top:8px;color:#8b8c8d;cursor:pointer;border:1px solid #dddedf;text-transform:uppercase;-webkit-transition:.1s all;transition:.1s all;font-size:10px;outline:none
}
.box button:hover{border-color:#c7c8c9;color:#6f7071
}
.box .large-btn{width:100%;background:#fff
}
.box .large-btn span{font-weight:600
}
.box .large-btn:hover{color:#fff !important
}
.box .button-set{margin-bottom:8px
}
.box #add-btn,.box #cancel-btn{margin-left:8px
}
.box .autocomplete-fix{position:absolute;visibility:hidden;overflow:hidden;opacity:0;width:0;height:0;left:0;top:0
}
.pop-out-enter-active,.pop-out-leave-active{-webkit-transition:all .5s;transition:all .5s
}
.pop-out-enter,.pop-out-leave-active{opacity:0;-webkit-transform:translateY(24px);transform:translateY(24px)
}